            <router-link :to="{path}">{{title}}</router-link>
        </li>
    </ul>
</div></template>

<script>
export default {
    data() {
        return {
            nodeTree: [{
                title: '',
                child: [
                    { path: 'overview', title: '' }
                ]
            },
            {
                title: '',
                child: [
                    { path: 'designstyle', title: '' },
                    { path: 'base', title: '' },
                    { path: 'button', title: '' },
                    { path: 'font', title: '' }
                ]
            },
            {
                title: '',
                child: [
                    // { path: 'modal', title: '' },
                    { path: 'table', title: '' },
                    { path: 'search', title: '' },
                    { path: 'autocomplete', title: '' },
                    { path: 'pager', title: '' },
                    { path: 'minimsg', title: '' },
                    { path: 'datepicker', title: '' }
                ]
            },
            {
                title: '',
                child: [
                    { path: 'template1', title: '1' }
                ]
            }
            ]
        };
    },
    created() {}
};
</script>

<style lang="css">
</style>



