<script>
import Page from 'ct-adc-page';
import Markdown from 'common/markdown';
import CodeSnippet from 'common/code-snippet';
export default {
    components: {
        Page,
        Markdown,
        CodeSnippet
    },
    data() {
        return {
            pageCfg: {
                pageIndex: 1,
                count: 200,
                pageSize: 20
            }
        };
    },
    methods: {
        changePage(page) {
            this.pageCfg.pageIndex = page;
